@keyframes shadow-drop-center {
    0% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }


body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: var(--charcoal);
    color: var(--dark);
}

.sec_card {
    width: 350px;
    box-sizing: border-box;
    padding: 40px;
    background-color: var(--white);
    border-radius: 30px;
}

.sec_h1{
    font-size: 40px;
    letter-spacing: -0.5px;
    line-height: 40px;
    margin-bottom: 15px;
    margin-top: 15px;
}

.sec_p{
    font-size: 14px;
    margin-bottom:20px;
}

.sec_button {
    width: 100%;
    box-sizing: border-box;
    padding: 14px 21px;
    border: none;
    background-color: var(--dark);
    outline: none;
    border-radius: 5px;
    color: var(--white);
    cursor: pointer;
}

button {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    transition: box-shadow 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940);
  }
  
button:hover {
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    background: linear-gradient(90deg, rgba(255, 84, 161, 1) 0%, rgba(255, 80, 0, 1) 100%);
}

.sec_img {
    width: 50px;
}