@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@600&family=Poppins&family=Roboto&display=swap');

:root {
  --grey: hsl(231, 7%, 60%);
  --white: hsl(0, 0%, 100%);
  --charcoal: hsl(235, 18%, 26%);
  --dark: hsl(234, 29%, 20%);
  --font:'Roboto', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Epilogue', sans-serif;
  font-family: 'Poppins', sans-serif;
  font-family: 'Roboto', sans-serif;
}


