@keyframes shadow-drop-center {
    0% {
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }


body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: var(--charcoal);
}

.card {
    width: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 5px 20px;
    background-color: var(--white);
    border-radius: 30px;
}

.left {
    box-sizing: border-box;
    padding: 30px;
    color: var(--dark);
    width: 60%;
    font-size: 14px;
}

.left h1{
    font-size: 50px;
    margin-bottom: 20px;
}

.left p{
    
    margin-bottom:20px;
}

li {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
}

label {
    display: block;
    margin-bottom: 5px;
    margin-top: 25px;
    font-size: 12px;
    font-weight: bolder;
}

input {
    width: 90%;
    box-sizing: border-box;
    padding: 13px 20px;
    border: 1px solid var(--charcoal);
    border-radius: 5px;
    margin-bottom: 15px;
}

input:focus {
    outline: none;
}

button {
    width: 90%;
    box-sizing: border-box;
    padding: 14px 21px;
    border: none;
    background-color: var(--dark);
    outline: none;
    border-radius: 5px;
    color: var(--white);
    cursor: pointer;
}

ul {
    list-style-type: none;
}

.icon_list {
    margin-right: 10px;
}

.right {
    width: 40%;
}

.right img{
    width: 100%;
}

.label_div {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.error_text {
    color: red;
    font-style: italic;
}

.error_input {
    border-color: red;
    background-color: rgba(255, 0, 0, 0.1);
}

.error_input::placeholder {
    color: red;
}
  
button {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    transition: box-shadow 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940);
  }
  
  button:hover {
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    background: linear-gradient(90deg, rgba(255, 84, 161, 1) 0%, rgba(255, 80, 0, 1) 100%);
  }

